import * as React from "react"
import { graphql, useStaticQuery } from "gatsby"
import "../styles/_price_chart.scss"

// button
// import ButtonMore from "../components/button-more"


const PriceChart = () => {
  const data = useStaticQuery(graphql`
    query {
      left: allPriceChartJson(filter: {data: {keytext: {eq: "left"}}}) {
        edges {
          node {
            date
            price
            sale
          }
        }
      }
      right: allPriceChartJson(filter: {data: {keytext: {eq: "right"}}}) {
        edges {
          node {
            date
            price
            sale
          }
        }
      }
    }
  `)
  
  const prices = data.left.edges
  const prices_R = data.right.edges
  


  return (
    <>
        <div className="price-chart">

          <div className="priceBox">
            <dl className="flex__wrap price-chart__row price-chart__head">
              <dt className="bdr-white bg--dark-green text-white flex-col-3 text-center is-center day"></dt>
              <dd className="bdr-white bg--dark-green text-white flex-col-3 text-center is-center"></dd>
              <dd className="bdr-white bg--pink text-white flex-col-3 text-center price__head--sale "><span className="nowrap"></span></dd>
            </dl>

            {prices.map(e => (
              <dl className="flex__wrap text-center price-chart__row">
                <dt className="text-white bdr-white bg--dark-green flex-col-3 text-sm text-medium is-center day">{e.node.date}</dt>
                <dd className="bg--light-yellow text-deep-gr bdr-white flex-col-3 text-sm is-center price--normal">{e.node.price}<span className="text-xs"></span></dd>
                <dd className="bdr-white text-lg text-medium flex-col-3 bg--light-gray text-pk price--sale">{e.node.sale}<span className="en"></span></dd>
              </dl>
            ))}
          </div>


          <div className="priceBox price__second">
            {/*  */}
            <dl className="flex__wrap price-chart__row price-chart__head">
                <dt className="bdr-white bg--dark-green text-white flex-col-3 text-center is-center day"></dt>
                <dd className="bdr-white bg--dark-green text-white flex-col-3 text-center is-center"></dd>
                <dd className="bdr-white bg--pink text-white flex-col-3 text-center price__head--sale "><span className="nowrap"></span></dd>
              </dl>
              {prices_R.map(e => (
                <dl className="flex__wrap text-center price-chart__row">
                  <dt className="text-white bdr-white bg--dark-green flex-col-3 text-sm is-center day">{e.node.date}</dt>
                  <dd className="bg--light-yellow text-deep-gr bdr-white flex-col-3 text-sm is-center price--normal">{e.node.price}<span className="text-xs"></span></dd>
                  <dd className="bdr-white text-lg text-medium flex-col-3 bg--light-gray text-pk price--sale">{e.node.sale}<span className="en"></span></dd>
                </dl>
              ))}
          </div>
        </div>
        

          {/* <div className="price-chart price__second">
            <dl className="flex__wrap price-chart__row price-chart__head">
              <dt className="bdr-white bg--dark-green text-white flex-col-3 text-center is-center day"></dt>
              <dd className="bdr-white bg--dark-green text-white flex-col-3 text-center is-center"></dd>
              <dd className="bdr-white bg--pink text-white flex-col-3 text-center price__head--sale "><span className="nowrap"></span></dd>
            </dl>

            {prices.map(e => (
              <dl className="flex__wrap text-center price-chart__row">
                <dt className="text-white bdr-white bg--dark-green flex-col-3 text-sm is-center day">{e.node.date}</dt>
                <dd className="bg--light-yellow text-deep-gr bdr-white flex-col-3 text-sm is-center price--normal">{e.node.price}<span className="text-xs"></span></dd>
                <dd className="bdr-white text-md flex-col-3 bg-light-gray text-pk price--sale">{e.node.sale}<span className="en"></span></dd>
              </dl>
            ))}
          </div> */}

    <p className="priceNote text-xxs">12/291/11,0001,100<br />Web()</p>
  </>

)}

export default PriceChart